import React from "react";
import PropTypes from "prop-types";

export default class ProductDetailsProductDescription extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        console.log("product details - mounting component");
    }

    toHTML(content) {
        return {__html: content};
    }

    render() {
        if(!this.props.description)
            return (null);
        return (
            <div id={'product-description-expander'} className="js-expander__container opl-wiking-expander__container is-expanded">
                <h2 id={'product-description'} data-skiplinks={'Opis produktu'}
                   className="h2 u-padding-l u-small-no-padding-b u-small-padding-left-xl u-no-spacing">Opis</h2>
                <a href="#" className="js-expander__trigger opl-wiking-expander__trigger">
                    <span className="g-icon g-icon--only g-icon--arrow-down-slim"></span>
                    <span className="u-acc-hide">rozwiń opis</span>
                </a>
                <div className="js-expander__content opl-wiking-expander__content u-spacing-xl">
                    <div className="u-small-padding-top-l">
                        <div id={'product-details-gradient'} data-js-module="common/modules/opl-gradient-fade"
                             data-js-options="{&quot;hideTrigger&quot;: false, &quot;triggerHideName&quot;: &quot;Zwiń&quot;, &quot;triggerShowName&quot;: &quot;Więcej&quot;, &quot;showFirst&quot;: 0, &quot;scrollSet&quot;: 80, &quot;loadAll&quot;: true }"
                             data-js-conditions="element:{was seen}" className="opl-gradient-fade">
                            <div className="opl-gradient-fade--wrapper">
                                <div className="opl-gradient-fade--cover"></div>
                                <div className="opl-gradient-fade--item"
                                     dangerouslySetInnerHTML={this.toHTML(this.props.description)}>
                                </div>
                            </div>
                            <div className="opl-gradient-fade--trigger u-spacing-top"><a href="#"
                                                                                         className="opl-load-more u-font-bold">Więcej</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }


}

ProductDetailsProductDescription.propTypes = {
    description: PropTypes.string.isRequired
};
